@font-face {
    font-family: chocolatedonut;
    src: url("fonts/chocolatedonut.otf");
}

@font-face{
    font-family: cream;
    src: url("fonts/cream.ttf");
}

h1{
    font-family: chocolatedonut;
    position:relative;
}

.ingredient-add {
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 844px;
    /* padding-top: 100px; */
    margin: auto 0;
}
  
.image-section {
    background: linear-gradient(180deg, rgb(255, 149.81, 149.81) 0%, rgba(255, 204.7, 204.7, 0.48) 53.65%, rgba(255, 255, 255, 0) 100%);
    border: 5px solid #DC4733;
    border-radius: 25px;
    height: 352px;
    width: 352px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color:#DC4733;
    font-family: cream;
}

.image-upload-label {
    cursor: pointer;
    display: block;
}

.image-upload-input {
    display: none; 
}

.uploaded-image {
    height: 352px;
    width: 352px;
    object-fit: cover;  
}

label.input-label{
    font-family: chocolatedonut;
    font-size: 20px;
    justify-content: center;
    text-transform: uppercase;
    padding-bottom: 5px;
}
  
.input-label {
    /* border: solid 1px black;
    color: red; */
    /* font-family: "Inter-Bold", Helvetica; */
    font-size: 14px;
    font-weight: 700;
    margin-top: 50px;
    font-family: chocolatedonut;
}
  
.input-box input {
    width: 100%; 
    height: 100%; 
    border: none; 
    background: transparent; 
    padding: 0 10px; 
    font-size: 14px; 
    color: #000000;
    outline: none; 
}

.input-box input::placeholder {
    color: #000000;
    font-family: cream;
}

.input-box {
    background: linear-gradient(180deg, rgb(255, 173, 173) 0%, rgba(255, 173, 173, 0.45) 55.24%, rgba(255, 173, 173, 0) 100%);
    height: 41px;
    width: 242px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
    font-size: 14px;
}

button.add-button {
    background-color: #DC4733;
    border: 2px solid #DC4733;
    border-radius: 15px;
    height: 41px;
    width: 242px;
    margin-top: 50px;
    margin-left: 20px;
    color: white;
    font-family: cream;
    font-size: 14px;
    font-weight: lighter;
    /* font-weight: 700; */
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    transition: background-color 0.3s, box-shadow 0.3s; /* smooth transition for hover effect */
}

/* button.add-button{
    font-family: cream;
} */

button.add-button:hover {
    box-shadow: 0 0 20px 0 rgba(220, 71, 51, 0.9);
    background-color: #ff5a3c;
    
}
