.columns {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.column {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  max-width: 300px; 
  text-align: center;
}

.column a {
  color: inherit; 
  text-decoration: none; 
  display: block; 
}


.column img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.recipe-description h3 {
  font-size: 1.2rem;
  margin: 0;
}

.recipe-description p {
  font-size: 1rem;
  color: #333;
}

